"use client";

import { useState } from 'react';
import { ArrowLeft, Clock, Star, CheckCircle, Clock as ClockIcon, Calendar, User } from 'lucide-react';

type SkillType = 'learned' | 'taught';
type SkillStatus = 'scheduled' | 'ongoing' | 'completed';

interface SkillCardProps {
  title: string;
  partnerName: string;
  date: string;
  duration: number;
  rating: number;
  status: SkillStatus;
  type: 'learned' | 'taught';
}

const SkillCard = ({ title, partnerName, date, duration, rating, status, type }: SkillCardProps) => {
  const statusColors = {
    scheduled: 'bg-yellow-100 text-yellow-800',
    ongoing: 'bg-blue-100 text-blue-800',
    completed: 'bg-green-100 text-green-800',
  };

  const typeColors = {
    learned: 'bg-purple-100 text-purple-800',
    taught: 'bg-cyan-100 text-cyan-800',
  };

  return (
    <div className="bg-white rounded-xl p-4 shadow-sm border border-gray-100 mb-4">
      <div className="flex justify-between items-start mb-2">
        <div>
          <div>
            <h3 className="font-medium text-gray-900">{title}</h3>
            <p className="text-sm text-gray-500">with {partnerName}</p>
          </div>
        </div>
        <div className="flex space-x-2">
          <span className={`text-xs px-2 py-1 rounded-full ${typeColors[type]}`}>
            {type.charAt(0).toUpperCase() + type.slice(1)}
          </span>
          <span className={`text-xs px-2 py-1 rounded-full ${statusColors[status]}`}>
            {status.charAt(0).toUpperCase() + status.slice(1)}
          </span>
        </div>
      </div>
      
      <div className="flex items-center text-sm text-gray-500 mb-2">
        <Calendar className="h-4 w-4 mr-1" />
        <span className="mr-4">{date}</span>
        <Clock className="h-4 w-4 mr-1" />
        <span>{duration}h</span>
      </div>
      
      <div className="flex justify-between items-center">
        <div className="flex">
          {[1, 2, 3, 4, 5].map((star) => (
            <Star 
              key={star} 
              className={`h-4 w-4 ${star <= rating ? 'text-yellow-400 fill-yellow-400' : 'text-gray-300'}`} 
            />
          ))}
        </div>
        
        {status === 'completed' && (
          <button className="text-sm text-blue-600 hover:text-blue-800 flex items-center">
            <CheckCircle className="h-4 w-4 mr-1" />
            Completed
          </button>
        )}
        {status === 'ongoing' && (
          <button className="text-sm text-blue-600 hover:text-blue-800 flex items-center">
            <ClockIcon className="h-4 w-4 mr-1" />
            In Progress
          </button>
        )}
      </div>
    </div>
  );
};

const SkillHistoryPage = () => {
  const [activeTab, setActiveTab] = useState<'all' | 'learned' | 'taught'>('all');
  const [searchQuery, setSearchQuery] = useState('');

  // Mock data - replace with real data from your API
  const skills: SkillCardProps[] = [
    {
      title: 'UI/UX Design',
      partnerName: 'Alex Johnson',
      date: '2023-11-10',
      duration: 2,
      rating: 4,
      status: 'completed',
      type: 'learned',
    },
    {
      title: 'React Native',
      partnerName: 'Sarah Miller',
      date: '2023-11-12',
      duration: 1.5,
      rating: 5,
      status: 'ongoing',
      type: 'taught',
    },
    {
      title: 'Project Management',
      partnerName: 'Michael Chen',
      date: '2023-11-15',
      duration: 1,
      rating: 0,
      status: 'scheduled',
      type: 'learned',
    },
  ];

  const filteredSkills = skills.filter((skill) => {
    const matchesTab = activeTab === 'all' || skill.type === activeTab;
    const matchesSearch = searchQuery === '' || 
      skill.title.toLowerCase().includes(searchQuery.toLowerCase()) || 
      skill.partnerName.toLowerCase().includes(searchQuery.toLowerCase());
    return matchesTab && matchesSearch;
  });

  return (
    <div className="min-h-screen bg-gray-50">
      {/* Header */}
      <header className="bg-white shadow-sm p-4 sticky top-0 z-10">
        <div className="flex items-center">
          <button className="p-2 -ml-2">
            <ArrowLeft className="h-5 w-5 text-gray-600" />
          </button>
          <h1 className="text-lg font-semibold text-gray-900 ml-2">Skill History</h1>
        </div>
      </header>

      <div className="p-4">
        {/* Search Bar */}
        <div className="relative mb-4">
          <div className="absolute inset-y-0 left-0 pl-3 flex items-center pointer-events-none">
            <svg className="h-5 w-5 text-gray-400" fill="none" viewBox="0 0 24 24" stroke="currentColor">
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M21 21l-6-6m2-5a7 7 0 11-14 0 7 7 0 0114 0z" />
            </svg>
          </div>
          <input
            type="text"
            className="block w-full pl-10 pr-3 py-2 border border-gray-300 rounded-lg bg-white shadow-sm focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent"
            placeholder="Search skills..."
            value={searchQuery}
            onChange={(e) => setSearchQuery(e.target.value)}
          />
        </div>
        
        {/* Segmented Control */}
        <div className="inline-flex rounded-lg bg-gray-100 p-1 w-full">
          {[
            { id: 'all', label: 'All' },
            { id: 'learned', label: 'Learned' },
            { id: 'taught', label: 'Taught' },
          ].map((tab) => (
            <button
              key={tab.id}
              onClick={() => setActiveTab(tab.id as 'all' | 'learned' | 'taught')}
              className={`px-4 py-2 text-sm font-medium rounded-md flex-1 ${
                activeTab === tab.id
                  ? 'bg-white shadow-sm text-blue-600'
                  : 'text-gray-500 hover:text-gray-700'
              }`}
            >
              {tab.label}
            </button>
          ))}
        </div>
      </div>

      {/* Skill Cards */}
      <div className="p-4">
        {filteredSkills.map((skill, index) => (
          <SkillCard key={index} {...skill} />
        ))}
      </div>
    </div>
  );
};

export default SkillHistoryPage;